import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [1,2,3,4,5,6,7,8,9,0,11,22,33,44],
			loadingType: 0,
			page: 1,
			typeList: ['综合', '最新', '价格', '销量'],
			curType: 0
		}
	},
	methods: {
		changeType(index){
			this.curType = index;
		},
		async getList(){
			setTimeout(() => {
				uni.stopPullDownRefresh();
				this.list = this.list.concat([1,2,3,4,5,6,7,8,9,0,11,22,33,44]) ;
				if(this.list.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}, 3000);
		},
		goToDetail(){
			uni.navigateTo({
			url: '/pages/express_detail/express_detail'
		});
		}
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.getList();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.getList();
	},
	onLoad(){
		
	}
	
}
